@import url('https://fonts.googleapis.com/css2?family=Syne+Mono&display=swap');
.header{
    color: white;
    margin-top: 70px;
    margin-left: 10px;
}
.header__icons{
    display: flex;
    align-items: center;
    text-align: center;
    cursor: pointer;
}
.MuiGrid-root>.header__icon{
    display: flex;
    flex-direction: column;
    width: 100%;
}
.header__icon{
    padding-right: 40px;
}

.header__icon > p{
    display: none;
    text-transform: uppercase;
    letter-spacing: 3px;
    margin-left: auto;
    margin-right: auto;
    font-size: small;
}

.header__icon:hover>p{
    display: inline-flex !important;
}

.header__icon > .MuiSvgIcon-root{
    margin-left: auto;
    margin-right: auto;
}

.header>h1{
    margin-top: 40px;
    margin-bottom: 100px;
    font-size: 2rem;
    font-family: 'Syne Mono', monospace;;
}

.header>h1>span{
    font-family: 'Syne Mono', monospace;
    font-size: 3rem;
    color: red;
}

.header__icon--active >p{
    display: inline-flex  !important;
}
