@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');

.nav{
    color: white;
    padding: 10px;
}

.nav >h3{
    font-family: 'Montserrat', sans-serif;
    cursor: pointer;
    margin: 5px;
    font-weight: 500;
    opacity: 0.7;
    transition: transform 100ms ease-in-out;
}

.nav>h3:hover{
    opacity: 1;
    transform: scale(1.2);
}