.videoCard{
    color:#ffffff;
   width: 400px;
    height: 350px;
    padding: 10px;
    transition: transform 100ms ease-in;
}

.videoCard:hover{
    transform: scale(1.09);
}

.videoCard >img{
    height: 250px;
    object-fit: contain;
    max-width: 350px;
}

.videoCard >h2{
    padding-top: 10px;
}

.videoCard__stats{
    display: none;
}

.videoCard:hover> .videoCard__stats{
    display: inline-flex;
}